/* Overall container styling with a formal, clean look */
.application-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f9fa; /* Light grey background for a formal look */
    font-family: 'Verdana', sans-serif;
    text-align: center;
    padding: 20px;
  }
  
  /* Header with formal colors and styling */
  .application-container h1 {
    font-size: 2.5rem;
    color: #002147; /* Dark navy blue for a professional look */
    margin-bottom: 20px;
    text-transform: uppercase; /* Capitalize the heading for a formal feel */
    font-weight: bold;
    border-bottom: 4px solid #0056b3; /* A blue underline to create a government-like branding */
    padding-bottom: 10px;
  }
  
  /* Description text */
  .application-container p {
    font-size: 1.2rem;
    color: #333; /* Dark grey for readability */
    margin-bottom: 30px;
  }
  
  /* Style for the options container */
  .options {
    display: flex;
    gap: 30px; /* Space between buttons */
    flex-wrap: wrap; /* Make buttons responsive and wrap if necessary */
    justify-content: center;
  }
  
  /* Button styling for a more formal look */
  .option-button {
    padding: 12px 40px;
    font-size: 1.1rem;
    font-weight: bold;
    color: white; /* White text for buttons */
    background-color: #0056b3; /* Navy blue button background */
    text-decoration: none;
    border-radius: 4px; /* Slightly rounded corners */
    border: 2px solid #003366; /* Darker blue border for contrast */
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    text-transform: uppercase;
  }
  
  /* Hover effect for buttons */
  .option-button:hover {
    background-color: #003366; /* Darker blue on hover */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2); /* Add shadow on hover for a slight depth */
  }
  
  /* Responsive adjustments for mobile and smaller screens */
  @media (max-width: 768px) {
    .application-container h1 {
      font-size: 2rem; /* Reduce heading size on smaller screens */
    }
  
    .application-container p {
      font-size: 1rem; /* Adjust paragraph font size */
    }
  
    .option-button {
      padding: 10px 25px; /* Adjust button padding for smaller screens */
      font-size: 1rem;
    }
  
    .options {
      gap: 15px; /* Reduce space between buttons on smaller screens */
    }
  }