/* Container for the entire login page */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f8f9fa; /* Light grey background to create a formal, government-like look */
    font-family: 'Verdana', sans-serif;
  }
  
  /* Image container styling */
  .image-container img {
    width: 120px; /* Government pages typically use simple logos */
    margin-bottom: 20px;
  }
  
  /* Main login form container */
  .login-container {
    background-color: white;
    border: 2px solid #002147; /* Dark navy border for a formal, secure feel */
    border-radius: 10px;
    padding: 40px;
    width: 350px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    text-align: center;
  }
  
  /* Form header with government-style bold typography */
  .header h3 {
    color: #0056b3; /* Navy blue heading to reinforce authority */
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .header p {
    color: #333; /* Dark grey text for readability */
  }
  
  /* Form inputs styling */
  .inputs {
    margin-bottom: 20px;
  }
  
  .input input {
    width: 90%;
    padding: 10px;
    border: 1px solid #ced4da; /* Light border for input fields */
    border-radius: 5px;
    font-size: 1rem;
    background-color: #e9ecef; /* Light grey input background for simplicity */
    background-size: 20px 20px;
    background-position: 10px center;
    background-repeat: no-repeat;
    padding-left: 40px;
    margin-bottom: 15px;
  }
  
  /* Styling for the "Forgot Password" section */
  .forgotpassword {
    font-size: 0.9rem;
    color: #0056b3; /* Navy blue text for links */
    cursor: pointer;
  }
  
  .forgotpassword span:hover {
    text-decoration: underline;
  }
  
  /* Checkbox styling for remember me */
  .remember-me {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .remember-me input {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }
  
  /* Login button */
  .submit button {
    background-color: #0056b3; /* Government style navy blue */
    color: white;
    font-size: 1.1rem;
    font-weight: bold;
    padding: 10px 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    transition: background-color 0.3s ease;
  }
  
  .submit button:hover {
    background-color: #003366; /* Darker blue on hover */
  }
  
  /* Sign Up section */
  .signup-text p {
    font-size: 0.9rem;
    color: #333;
  }
  
  .signup-button {
    background-color: #28a745; /* Green button for sign-up (positive action) */
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .signup-button:hover {
    background-color: #218838; /* Darker green on hover */
  }
  
  /* Back to home button styling */
  .signup-button[align="center"] {
    margin-top: 20px;
    background-color: #6c757d; /* Grey for the home button */
  }
  
  .signup-button[align="center"]:hover {
    background-color: #5a6268; /* Darker grey on hover */
  }
  
  /* Responsive adjustments for smaller screens */
  @media (max-width: 768px) {
    .login-container {
      width: 90%;
    }
  
    .submit button {
      padding: 8px 20px;
    }
  
    .signup-text p {
      font-size: 0.8rem;
    }
  }